.profile {
    display: flex;
}

.photo-cont {
    width: 50%;
    padding: 20px;
}

.photo-cont img {
    width: 80%;
}

.info-cont {
    width: 50%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.info-cont h2 {
    font-size: 20px;
    height: 20px;
    margin: 10px 0;
}

.info-cont p {
    font-size: 16px;
    height: 16px;
    margin: 10px 0;
}

.info-cont a {
    text-decoration: none;
}

.info-cont .button {
    margin: 10px 0;
}